<template>
  <div id="app">
    <div class="urna">
      <TelaUrna
      :tela="telaAtual"
      :numeroVoto="numeroVoto"
      :quantidadeNumeros="quantidadeNumeros"
      :candidato="candidato"
      />
      <TecladoUrna 
      :adicionarNumero="adicionarNumero"
      :corrigir="corrigir"
      :confirmar="confirmar"
      :votarEmBranco="votarEmBranco"
      />
    </div>
  </div>
</template>

<script>

import '@/css/global.css';
import confirmAudio from '@/assets/audios/confirm.wav';
import keyAudio from '@/assets/audios/key.wav';

import TelaUrna from '@/components/Tela';
import TecladoUrna from '@/components/Teclado';

export default {
  name: 'App',
  components: {
    TecladoUrna,
    TelaUrna
  },
  methods: {
    adicionarNumero(numero){

      this.executarSom(keyAudio);
      
      if(this.numeroVoto.length == this.quantidadeNumeros) {
        return false;
      }

      this.numeroVoto += ''+numero;

      this.verificarCandidato();
    },

    verificarCandidato(){
      if(this.numeroVoto.length < this.quantidadeNumeros) {
        return false;
      }

      //Verifica se tem candidato no JSON
      if(this.candidatos[this.telaAtual][this.numeroVoto]){
        this.candidato = this.candidatos[this.telaAtual][this.numeroVoto];
        return true;
      }

      //Regra para voto nulo
      this.candidato = {
        nome: 'Voto Nulo',
        partido: 'Voto Nulo',
        imagem: ''
      }
    },

    corrigir(){
      this.executarSom(keyAudio);
      
      this.limpar();
    },

    limpar(){
      this.candidato = {}
      this.numeroVoto = ''
    },

    confirmar(){
      if(this.numeroVoto.length < this.quantidadeNumeros) {
        return false;
      }

      return this.avancarTela();
    },

    avancarTela(){
      this.executarSom(confirmAudio);

      if(this.telaAtual=='prefeito'){
        this.telaAtual = 'vereador';
        this.quantidadeNumeros = 5;
        return this.limpar();
      }

      this.telaAtual = 'fim';

      var instanciaAtual = this;

      setTimeout(function(){
        instanciaAtual.telaAtual = 'prefeito';
        instanciaAtual.quantidadeNumeros = 2;
        return instanciaAtual.limpar();
      },3000);
    },

    votarEmBranco(){
      if(this.telaAtual == 'fim'){
        return false;
      }

      this.limpar();
      this.avancarTela();
    },

    executarSom(arquivoSom){
      if(arquivoSom){
        let audio = new Audio(arquivoSom);
        audio.play();
      }
    }
  },
  data(){
    return {
      telaAtual: 'prefeito',
      numeroVoto: '',
      quantidadeNumeros: 2,
      candidato: {},
      candidatos: {
        "prefeito":{
          "01":{
            "nome": "Naruto",
            "partido": "Uzumaki",
            "imagem": "https://i.postimg.cc/L68KDLSj/naruto.png"
          },
          "05":{
            "nome": "Sasuke",
            "partido": "Uchiha",
            "imagem": "https://i.postimg.cc/tJ20yC31/sakura.webp"
          },
          "10":{
            "nome": "Sasuke",
            "partido": "Uchiha",
            "imagem": "https://i.postimg.cc/Vk2xzhCF/Sasuke.webp"
          }
        },
        "vereador":{
          "01234":{
            "nome": "Itachi",
            "partido": "Akatsuki",
            "imagem": "https://i.postimg.cc/RhMrjnH5/Itachi.png"
          },
          "43210":{
            "nome": "Kakashi",
            "partido": "Uchiha",
            "imagem": "https://i.postimg.cc/gkt9TjcQ/kakashi.png"
          },
          "78963":{
            "nome": "Kisame",
            "partido": "Akatsuki",
            "imagem": "https://i.postimg.cc/0y6gRvbQ/kisame.png"
          },
          "32100":{
            "nome": "Madara",
            "partido": "Uchiha",
            "imagem": "https://i.postimg.cc/9XpHy16W/madara.webp"
          }
        }
      }
    }
  }
}
</script>

